.emoji-keyboard {
  flex-grow: 1;
  padding-top: 10px;
  padding-left: 9px;
  overflow-y: auto;
}

.emoji-keyboard::-webkit-scrollbar {
  width: 16px;
}

.emoji-keyboard::-webkit-scrollbar-thumb {
  background-color: #dae2ea;
  border-radius: 10px;
  box-shadow: inset 0 0 0 5px #ffffff;
}

.emoji-keyboard__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.emoji-keyboard__item {
  margin-bottom: 10px;
}
